import styled from 'styled-components';

export const Wrapper = styled.div`
    display: grid;
    grid-template-columns: auto auto;
`;


export const Delete = styled.button`
    justify-self: end;
    margin-right: 20px;
    width: 14px;
    height: 18px;
    margin-top: 1.875rem;
    cursor: pointer;
    border: none;
    background: white;
    .icons8-trash { 
        display: inline-block;
        width: 30px;
        height: 30px;
        background: url('data:image/svg+xml;base64,') 50% 50% no-repeat;
        background-size: 100%; 
    }
`;

export const DeleteAll = styled.button`
    grid-column: 2;
    margin-top: 38px;
    display: flex;
    justify-self: end;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    border: none;
    cursor: pointer;
    width: 124px;
    height: 40px;
    background: #EB5757;
    border-radius: 4px;
    .icons8-trash { 
        display: inline-block;
        width: 15px;
        height: 15px;
        background: url('data:image/svg+xml;base64,') 50% 50% no-repeat;
        background-size: 100%; 
    }
`;